   s_slideNextStart      = new EventEmitter<any>();
   s_slideNextEnd        = new EventEmitter<any>();
   s_slidePrevStart      = new EventEmitter<any>();
   s_slidePrevEnd        = new EventEmitter<any>();
   s_transitionStart     = new EventEmitter<any>();
   s_transitionEnd       = new EventEmitter<any>();
   s_touchStart          = new EventEmitter<any>();
   s_touchMove           = new EventEmitter<any>();
   s_touchMoveOpposite   = new EventEmitter<any>();
   s_sliderMove          = new EventEmitter<any>();
